@media screen and (min-width: 768px) {
  .overlay__img {
    max-width: 768px;
    height: 415px;
    background-image: url(../images/header/tablet/ice-cream.png),
      url(../images/header/tablet/ice.png),
      url(../images/header/tablet/ellipse.png);

    background-size: 152px, 257px 325px, 768px 67px;
    background-repeat: no-repeat;
    background-position: left 32px top 250px, left 418px top 90px,
      top 347px left 0;
  }
}

@media screen and (min-device-pixel-ratio: 2) and (min-width: 768px),
  screen and (min-resolution: 192dpi) and (min-width: 768px),
  screen and (min-resolution: 2dppx) and (min-width: 768px) {
  .overlay__img {
    background-image: url(../images/header/tablet/ice-cream@2x.png),
      url(../images/header/tablet/ice@2x.png),
      url(../images/header/tablet/ellipse@2x.png);
  }
}

@media screen and (min-width: 1200px) {
  .overlay__img {
    max-width: 1200px;
    height: 664px;
    background-image: url(../images/header/desktop/ice-cream.png),
      url(../images/header/desktop/ice.png),
      url(../images/header/desktop/ellipse.png);

    background-size: 225px, 447px 561px, 1200px 384px;
    background-repeat: no-repeat;
    background-position: left 17px top 374px, left 651px top 103px,
      left 1px top 280px;
  }
}

@media screen and (min-device-pixel-ratio: 2) and (min-width: 1200px),
  screen and (min-resolution: 192dpi) and (min-width: 1200px),
  screen and (min-resolution: 2dppx) and (min-width: 1200px) {
  .overlay__img {
    background-image: url(../images/header/desktop/ice-cream@2x.png),
      url(../images/header/desktop/ice@2x.png),
      url(../images/header/desktop/ellipse@2x.png);
  }
}

